import React from 'react'
import { Row, Col, Card, Table } from 'react-bootstrap';
import { getBalanceSheet } from '../../api/accounting/reports'
import Loader from '../../App/components/Reusables/Loader'
import Num from '../../App/components/Reusables/Num';
import Jhilikka from '../../App/components/Reusables/Jhilikka';



function BalanceSheet(props) {
    const [loading, setLoading] = React.useState(true)
    const [assets, setAssets] = React.useState({})
    const [liabilities, setLiabilities] = React.useState({});
    const [netProfit, setNetProfit] = React.useState(0)
    // const [equity, setEquity] = React.useState({})
    // const [draw, setDraw] = React.useState({})

    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            await getBalanceSheet({ action: 'get', filter: 'none', start: 0, end: 999999 }, controller.signal)
                .then(async (res) => {
                    console.log(res);
                    if (res.json.status) {
                        const liabs = res.json.liabilities;
                        liabs.meta_data.sum_current_amount *= -1;
                        console.log(liabs);
                        for (let x in liabs.sub_headers) {
                            liabs.sub_headers[x].meta_data.sum_current_amount *= -1;
                            for (let y in liabs.sub_headers[x].accounts) {
                                liabs.sub_headers[x].accounts[y].current_amount *= -1;
                            }
                        }
                        await setLiabilities({ ...res.json.liabilities });
                        await setNetProfit(liabs.net_profit);
                        await setAssets({ ...res.json.assets })
                        console.log(res.json.liabilities)
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort();
    }, [])

    const _expand = (index, side) => {
        if (side === 'assets') {
            const tempAssets = assets;
            tempAssets.sub_headers[index].isExpanded = false;
            setAssets({ ...tempAssets })
        }
        if (side === 'liabilities') {
            const tempLiabilities = liabilities;
            tempLiabilities.sub_headers[index].isExpanded = false;
            setLiabilities({ ...tempLiabilities })
        }
    }

    const _collapse = (index, side) => {
        if (side === 'assets') {
            const tempAssets = assets;
            tempAssets.sub_headers[index].isExpanded = true;
            setAssets({ ...tempAssets })
        }

        if (side === 'liabilities') {
            const tempLiabilities = liabilities;
            tempLiabilities.sub_headers[index].isExpanded = true;
            setLiabilities({ ...tempLiabilities })
        }
    }


    // console.log(assets)
    if (!loading) {
        var List = (input, type) => {
            return input.sub_headers.map((aRow, index) => {
                let ButtonElement = null;
                if (!aRow.isExpanded) {
                    ButtonElement =
                        <i
                            className="feather icon-minus-square text-c-green f-14 pl-1 pr-1"
                            style={{ color: '#10a9f5', cursor: 'pointer' }}
                            onClick={(e) => _collapse(index, type)}
                        />
                } else {
                    ButtonElement = <i
                        className="feather icon-plus-square text-c-green f-14 pl-1 pr-1"
                        style={{ color: '#10a9f5', cursor: 'pointer' }}
                        onClick={(e) => _expand(index, type)}
                    />
                }
                const AccountsList = aRow.accounts.map((ac, inx) => {
                    if (ac.current_amount === 0) return null;
                    return <tr onClick={() => props.history.push('/account/' + ac.uuid)} key={inx + 'r'} className='balanceSheetChildRow'>
                        <th></th>
                        <td className='balanceSheetChildData'  >{ac.name}</td>
                        <td className='balanceSheetChildData' ><Num>{ac.current_amount}</Num></td>
                    </tr >
                })

                if (aRow.meta_data.sum_current_amount === 0) return null;
                return [<tr key={index}>
                    <th></th>
                    <td>
                        {aRow.name}
                        {aRow.accounts.length > 0 ? ButtonElement : null}
                    </td>
                    <td ><Num>{aRow.meta_data.sum_current_amount}</Num></td>
                </tr>,
                ...(!aRow.isExpanded ? AccountsList : [])
                ]
            })
        }
    }

    if (loading) return <Loader />;
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Balance Sheet</h4>
                <div>
                    <span></span>
                </div>
            </Row>
            <br />
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <Table className="accountingReportTable" hover responsive>

                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Assets</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {List(assets, 'assets')}
                                </tbody>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th><u>Total</u></th>
                                        <th><u><Num>{assets.meta_data.sum_current_amount}</Num></u></th>
                                    </tr>
                                    <tr>
                                        <th>-</th>
                                        <td>-</td>
                                        <td>-</td>
                                    </tr>
                                </thead>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Capital and Liabilities</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr onClick={() => props.history.push('/profit-loss-account')} >
                                        <th></th>
                                        <td>Net Profit</td>
                                        <td><Num>{netProfit}</Num></td>
                                    </tr>
                                    {List(liabilities, 'liabilities')}
                                </tbody>
                                <tfoot>
                                    <tr >
                                        <th></th>
                                        <th><u>Total</u></th>
                                        <th><u><Num>{liabilities.meta_data.sum_current_amount + netProfit}</Num></u></th>
                                    </tr>
                                </tfoot>

                            </Table>
                        </Card.Body>
                    </Card >

                </Col>
            </Row>
        </>
    )
}

export default BalanceSheet
